<script>
import tabbar from "@/components/tabbar.vue";
export default {
  data() {
    return {
      value: "",
      tab: 0,
      no:false,
      loading: false,
      finished: false,
      loading2: false,
      finished2: false,
      test: "",
      follow: false //
    };
  },
  components: {
    tabbar: tabbar
  },
  methods: {
    onSearch: function() {
      if (this.value) {
        this.$toast(this.value);
      } else {
        this.$toast("");
      }
    },
    qhtab(index) {
      //
      this.$toast(index);
    },
    tozhubo() {
      //
      this.tab = 1;
    },
    tozb() {
      //
      this.tab = 2;
    },
    guanzhu(vid) {
      //
      this.$toast(vid);
      this.follow = true;
      this.$toast.success("");
    },
    onLoad() {
      //

      // 
      this.loading = false;
      // 
      this.finished = true;
    },
    onLoad2() {
      //

      // 
      this.loading2 = false;
      // 
      this.finished2 = true;
    }
  },
  mounted() {
    this.value = this.$route.query.val;
    if (this.value) {
      this.onSearch();
    }
  }
};
</script>
