<template>
  <!---->
  <div class="register">
    <div class="header">
      <van-nav-bar title="" />
    </div>
    <div class="dlbox">
      <van-cell-group>
        <van-field
          v-model.trim="tel"
          required
          label=""
          type="number"
          :error-message="err1"
          @input="testtel"
          placeholder=""
        />

        <van-field
          v-model.trim="password"
          type="password"
          label=""
          placeholder=""
          required
          :error-message="err2"
          @input="testmm"
        />
        <van-field
          v-model.trim="password2"
          type="password"
          label=""
          placeholder=""
          required
          :error-message="err3"
          @input="testmm2"
        />
        <van-field
          v-model.trim="sms"
          center
          label=""
          placeholder=""
        >
          <van-button
            slot="button"
            size="small"
            type="primary"
            :disabled="!flag"
            :text="txt"
            @click="yzm"
          ></van-button>
        </van-field>
      </van-cell-group>
      <div class="dlbtn">
        <van-button
          type="primary"
          size="large"
          @click="zhuce"
        ></van-button>
      </div>

    </div>

    <tabbar :findex="2" />
  </div>
</template>
<script>
import tabbar from "@/components/tabbar.vue";
export default {
  data() {
    return {
      tel: "",
      password: "",
      password2: "",
      txt: "",
      tim: 60,
      flag: true,
      sms: "",
      err1: "",
      err2: "",
      err3:""
    };
  },
  components: {
    tabbar: tabbar
  },
  methods: {
    testtel() { //
      if (this.tel.length < 11 || this.tel[0] != "1") {
        this.err1 = "";
      } else {
        this.err1 = "";
      }
    },
    testmm(){ //
      if (this.password.length < 6 || this.password.length > 20) {
        this.err2 = "620";
      } else {
        this.err2 = "";
      }
    },
    testmm2(){ //
      if (this.password2!=this.password) {
        this.err3 = "";
      } else {
        this.err3 = "";
      }
    },
    zhuce() {
      if (this.tel.length < 11 || this.tel[0] != "1") {
        this.err1 = "";
      } else if (this.password.length < 6 || this.password.length > 20) {
        this.err2 = "620";
      } else if(this.password2!=this.password){
        this.err3 = "";
      }else if(this.yzm.length==0){
        this.$toast('');
      }
    },
    countDown() {
      //
      this.flag=false;
      this.tim = this.tim - 1;
      this.txt = this.tim + "";
      if (this.tim == 0) {
        this.txt = "";
        this.flag = true;
        this.tim = 60;
        return;
      }
      setTimeout(() => {
        this.countDown();
      }, 1000);
    },
    yzm() {
      //
      if (this.flag) {
        if (this.tel.length < 11 || this.tel[0] != "1") {
          this.err1 = "";
        } else {
          this.countDown()
        }
      }
    }
  },
  mounted() {
    //
  }
};
</script>
<style lang="scss">
.register {
  .van-nav-bar__text,
  .van-nav-bar .van-icon {
    color: #333;
  }
  .dlbox {
    padding-top: 30px;
    .dlbtn {
      padding-top: 10px;
      text-align: center;
      button {
        margin-bottom: 10px;
      }
    }
  }
  .van-cell-group .van-cell {
    background: #fff !important;
    color: #333 !important;
  }
}
</style>

